<template>
  <van-list
    v-model="loading"
    :finished="finished"
    finish-text=""
    @load="onLoad"
  >
    <van-cell
      v-for="item in list"
      :key="item.art_id.toString()"
      :title="item.title"
      @click="$router.push({
        name: 'article',
        params: {
          articleId: item.art_id.toString()
        }
      })"
    ></van-cell>
  </van-list>
</template>
<script>
import { getSearch } from '@/api/search'
export default {
  name: 'SearchResult',
  props: {
    q: {
      type: String,
      required: true
    }
  },
  components: {},
  data () {
    return {
      loading: false,
      finished: false,
      list: [],
      page: 1, // 
      perPage: 10
    }
  },
  created () {},
  mounted () {},
  methods: {
    async onLoad () {
      // this.loading = true

      // 1. 
      const { data: res } = await getSearch({
        page: this.page, // 1
        per_page: this.perPage, // 
        q: this.q // 
      })

      // 2. 
      const { results } = res.data
      this.list.push(...results)

      // 3.  loading false
      this.loading = false

      // 4. 
      if (results.length) {
        this.page++
      } else {
        this.finished = true
      }
    }
  }
}
</script>
