<template>
  <van-cell-group>
    <van-cell title="">
      <template v-if="isDeleteShow">
        <span @click="$emit('update:search-histories', [])"></span>
        &nbsp;&nbsp;
        <span @click="isDeleteShow = false"></span>
      </template>
      <van-icon  v-else slot="right-icon" name="delete" @click="isDeleteShow = true"></van-icon>
    </van-cell>
    <van-cell
      :title="item"
      v-for="(item, index) in searchHistories"
      :key="index"
      @click="onHistoryClick(item, index)"
    ></van-cell>
  </van-cell-group>
</template>
<script>
export default {
  name: 'SearchHistory',
  model: {
    prop: 'search-histories',
    event: 'update: search-histories'
  },
  props: {
    searchHistories: {
      type: Array,
      default: () => []
    }
  },
  components: {},
  data () {
    return {
      isDeleteShow: false
    }
  },
  created () {},
  mounted () {},
  methods: {
    onHistoryClick (item, index) {
      if (this.isDeleteShow) {
        // 
        this.searchHistories.splice(index, 1)
      } else {
        // 
        this.$emit('search', item)
      }
    }
  }
}
</script>
<style scoped lang="less">
</style>
